/* ------- GENERAL ------- */
img {
  width: 100%;
}

a, a:hover {
  color: white;
}

a:link {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

ul li {
  margin: 0px 10px 0px 10px;
}

/* ------- MIXIN RACCOURCIS ------- */
/* ------- DECLARATION VARIABLES ------- */
/* ------- DEFINITION DES BACKGROUNDS POUR CHAQUE PAGE ------- */
.accueil {
  background-image: url("../img/accueil.jpg");
  background-repeat: no-repeat;
}

/* ------- STYLE POUR ECRAN TAILLE SMALL ------- */
@media (min-width: 0px) {
  .barNav {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: flex-end;
    justify-content: flex-start;
    background-color: #DD0426;
    min-height: 50px;
    height: auto;
    /* Aligner verticalement le label */
  }
  .barNav label {
    line-height: 40px;
  }
  .barNav .hamb {
    width: 40px;
    margin-right: 20px;
  }
  .barNav .dropdown {
    display: none;
  }
  .barNav .nav {
    display: none;
  }
  .barNav input[type='checkbox']:checked ~ .nav {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    font-size: 2em;
  }
}

/* ------- STYLE POUR ECRAN TAILLE MEDIUM ------- */
@media (min-width: 800px) {
  .barNav {
    height: 50px;
    display: flex;
    justify-content: center;
  }
  .barNav .hamb {
    display: none;
  }
  .barNav input[type='checkbox']:checked ~ .nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 1.3em;
    width: 100%;
  }
  .barNav .nav {
    height: 50px;
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: center;
    align-items: center;
    font-size: 1.3em;
  }
  .barNav .nav li {
    display: flex;
    flex-direction: column;
    padding-left: 20px;
    padding-right: 20px;
    height: 55px;
    justify-content: center;
  }
  .barNav .nav li:hover {
    background-color: #F02D3A;
  }
}
